function Navbar() {
  // const classes = useStyles();

  return (
    <AppBar position="static" color='secondary' sx={{background:'purple'}}>
      <CssBaseline />
      <Toolbar>
        <Typography variant="h4" sx={{fontFamily: 'monospace', color:"inherit"}} >
          Nishappened
        </Typography>
          <div >
            <Button>
          <Link to="/canvas" >
              Canvas
            </Link></Button>

            <Link to="/murals" >
              murals
            </Link>

          </div>
      </Toolbar>
    </AppBar>
  );
}
export default Navbar;